<template>
  <div class="wrapper">
    <div class="list-page">
      <div class="container">
        <div class="list-title">
          <div class="left">
            <div class="title">节点</div>
            <div class="total">
              总共
              <span>{{total}}</span>个节点
            </div>
          </div>
        </div>
        <div class="tableWrapper">
          <div class="tableminwrapper">
            <el-table :data="tableData" style="width: 100%">
              <el-table-column label="高度">
                <template scope="scoped">
                  <span
                    @click="toblockdetails(scoped.row)"
                    style="cursor:pointer"
                  >{{scoped.row.height}}</span>
                </template>
              </el-table-column>
              <el-table-column prop="timestamp.human" label="时间" width="180"></el-table-column>
              <el-table-column prop="transactions" label="数量"></el-table-column>
              <el-table-column label="ID">
                <template scope="scoped">
                  <span @click="toblockdetails(scoped.row)" style="cursor:pointer">
                    {{`${scoped.row.id.substring(
                    0,
                    6
                    )}...${scoped.row.id.substring(
                    scoped.row.id.length - 6
                    )}`}}
                  </span>
                </template>
              </el-table-column>
              <el-table-column prop="forged.fee" label="手续费">
                <template scope="scoped">{{(scoped.row.forged.fee/100000000).toFixed(2)}}M</template>
              </el-table-column>
            </el-table>
          </div>
        </div>
        <div class="changepage-wrapper">
          <el-pagination
            :pager-count="5"
            @current-change="pagen"
            :page-size="25"
            layout="prev, pager, next"
            :total="total"
          ></el-pagination>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import {
  getallnode,
  //  getblockAll
} from "../../../api";
import moment from "moment/moment";
export default {
  mounted() {
    getallnode({ orderBy: "timestamp:desc", page: 1, limit: 25 })
      .then((res) => {
        console.log(res)
        for (let v of res.data.data) {
          v.timestamp.human = moment(v.timestamp.human).format(
            "YYYY-MM-DD HH:mm:ss"
          );
          // v.generator.address = `${v.generator.address.substring(
          //   0,
          //   10
          // )}...${v.generator.address.substring(
          //   v.generator.address.length - 6
          // )}`;
        }
        this.total = res.data.meta.totalCount;
        this.tableData = res.data.data;
      })
      .catch((err) => {
        window.console.log(err);
      });
  },
  data() {
    return {
      tableData: [],
      total: 0,
    };
  },
  methods: {
    pagen(e) {
      getallnode({ orderBy: "timestamp:desc", page: e, limit: 25 })
        .then((res) => {
          console.log(res.data);
          for (let v of res.data.data) {
            v.timestamp.human = moment(v.timestamp.human).format(
              "YYYY-MM-DD HH:mm:ss"
            );
            // v.generator.address = ;
          }
          this.total = res.data.meta.totalCount;
          this.tableData = res.data.data;
        })
        .catch((err) => {
          window.console.log(err);
        });
    },
    toblockdetails(e) {
      console.log(e);
      this.$router.push({
        path: "/blockdetails",
        query: {
          id: e.height,
        },
      });
    },
  },
};
</script>

